@import "global/variables.scss";
@import "global/mixins.scss";
@import "global/patterns/**/*.scss";
@import "global/defaults.scss";
@import "global/breakpoint.scss";

@import "level-1/**/*.scss";

body,html {
  width: 100vw;
  margin: auto;
  overflow: hidden;
  overflow-y: scroll;
  font-family: 'Noto Sans TC', Arial, Helvetica, sans-serif;
}
.index {
  header {
    width: 100%;
    max-width: 1800px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 60px 61px 41px 50px;
    @include breakpoint($tablet) {
      justify-content: center;
    }
    @include breakpoint(max-width 1366px) {
      padding: 72px 61px 40px 50px;
    }
    @include breakpoint ($tablet) {
      padding: 72px 0px 40px 0px;
    }
    img {
      height: 60px;
      @include breakpoint($tablet) {
        height: auto;
        width: 340px;
      }
      @include breakpoint($phone) {
        height: auto;
        width: 90%;
      }
    }
    select {
      border: 1px solid #3D3D3D;
      border-radius: 24px;
      padding: 13px 18px;
      width: 144px;
      height: 48px;
      background-position-x: 110px;
      background-color: rgba(256,256,256,.6);
      box-shadow: rgb(0 0 0 / 15%) 0px 3px 6px;
      font-family: "Noto Sans";
      font-weight: 600;
      font-size: 16px;
      text-align: left;
      color: rgba(61, 61, 61, 0.7);
      @include breakpoint($tablet) {
        display: none;
      }
      option {
        color: rgba($color: #3D3D3D, $alpha: .7);
      }
      &:focus {
        outline: none !important;
        border: 1px solid #3D3D3D;
        border-radius: 24px;
      }
    }
  }
  
  .login {
    min-width: 770px;
    max-width: 770px;
    background-color: rgba(256,256,256,.6);
    backdrop-filter: blur(10px);
    box-shadow: rgb(0 0 0 / 15%) 0px 3px 6px;
    padding: 91px 97px 90px 91px;
    @include breakpoint($tablet) {
      min-width: 90%;
      max-width: 90%;
      border-radius: 15px;
      padding: 28px 38px 38px 38px;
    }
    @include breakpoint(min-width 1366px) {
      padding: 51px 97px 45px 97px;
    }
    .title-wrap{
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 24px;
      @include breakpoint($tablet) {
        margin-bottom: 59px;
      }
      a {
        text-decoration: none;
        font-family: "Noto Sans";
        font-weight: 600;
        font-size: 14px;
        line-height: 26px;
        text-align: center;
        color: rgba(61, 61, 61, 0.7);
        &::after {
          content: '|';
          margin: 0px 8px;
        }
        &:last-child {
          &::after {
            content: '';
            margin-right: 0;
          }
        }
        &.active {
          color: rgba(#3D3D3D, 1);
        }
      }
    }
    h1 {
      font-family: "Noto Sans";
      font-weight: bold;
      font-size: 36px;
      line-height: 42px;
      text-align: left;
      color: #3d3d3d;
      margin: 0;
    }
    .lang-mobile {
      display: none;
      @include breakpoint($tablet) {
        display: flex;
      }
    }
    .info {
      font-family: "Noto Sans";
      font-weight: 600;
      font-size: 16px;
      line-height: 22px;
      text-align: left;
      color: #3d3d3d;
      display: flex;
      width: 100%;
      justify-content: space-between;
      margin-bottom: 94px;
      @include breakpoint(min-width 1366px) {
        margin-bottom: 49px;
      }
      a {
        color: #0e4e96;
        text-decoration: none;
        margin-left: 6px;
      }
      .ver {
        @include breakpoint(min-width 1366px) {
          font-weight: normal;
          font-size: 13px;
        }
      }
      @include breakpoint($tablet) {
        flex-direction: column;
        font-size: 14px;
        margin-bottom: 15px;
        .ver {
          font-weight: normal;
          font-size: 12px;
        }
      }
    }
    .input {
      width: 576px;
      height: 48px;
      border-radius: 24px;
      background: #fff;
      border: 1px solid #3d3d3d;
      display: flex;
      align-items: center;
      margin-bottom: 16px;
      @include breakpoint($tablet) {
        width: 100%;
      }
      img {
        margin-left: 19px;
        &.eye {
          margin-right: 18px;
        }
      }
      input {
        border: 0;
        outline: 0;
        padding-left: 8px;
        width: 100%;
        height: 48px;
        font-family: "Noto Sans";
        font-weight: 600;
        font-size: 16px;
        text-align: left;
        color: #0e4e96;
        &::placeholder {
          font-family: "Noto Sans";
          font-weight: 600;
          font-size: 16px;
          text-align: left;
          color: rgba(#3D3D3D, 1);
        }
      }
      .store-wrap {
        &::after {
          content: url('../img/arrow.svg');
        }
      }
      select.store {
        width: 100%;
        border: 0;
        box-shadow: none;
        padding-left: 5px;
        height: 48px;
        font-family: "Noto Sans";
        font-weight: 600;
        font-size: 16px;
        text-align: left;
        color: rgba(#3D3D3D, .6) !important;
        &:focus {
          outline: none;
        }
      } 
    }
  }
  .btn-wrap,.block {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .blue-btn, .reset-btn {
      cursor: pointer;
      width: 280px;
      height: 48px;
      border-radius: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(14, 78, 150, 0.8);
      color: white;
      font-family: "Noto Sans";
      font-weight: 600;
      font-size: 18px;
      text-align: center;
      text-decoration: none;
      @include breakpoint($tablet) {
        width: 100%;
      }
      &:hover {
        background: #0e4e96;
        font-size: 19px;
        box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
      }
    }
    .reset-btn {
      background: rgba(61, 61, 61, 0.7);
      @include breakpoint($tablet) {
        margin-top: 15px;
      }
      &:hover {
        background: #3d3d3d;
      }
    }
  }
  .hide {
    display: none;
  }
  .eye {
    cursor: pointer;
  }
  select {
    appearance: none; 
    background-color: transparent;
    border: none;
    padding: 0 18px 0 0;
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    cursor: inherit;
    line-height: inherit;
    background: url(../img/arrow.svg);
    background-repeat: no-repeat;
    background-position: right 18px center;
    &::-ms-expand {
      display: none;
    }
  }
  
  footer {
    max-width: 1800px;
    margin-top: 71px;
    .logo-wrap {
      display: flex;
      align-items: center;
      justify-content: center;
      .bottom-logo {
        margin: 0 25px
      }
      .line {
        @include breakpoint($tablet) {
          display: none;
        }
      }
    }
    ul {
      list-style-type: none;
      color: white;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      font-family: "Noto Sans";
      font-weight: normal;
      font-size: 13px;
      letter-spacing: 0.03em;
      line-height: 19px;
      margin-bottom: 20px;
      padding-left: 0;
      @include breakpoint($tablet) {
        flex-direction: column;
        text-align: center;
      }
      @include breakpoint(min-width 1366px) {
        margin-bottom: 47px;
      }
      li {
        margin: 10px 12px;
      }
    }
  }
}

.error {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 9;
  background: rgba(61, 61, 61, 0.5);
  .block {
    width: 90%;
    max-width: 620px;
    display: flex;
    align-items: center;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 3px 6px rgb(0 0 0 / 16%);
    background: white;
    z-index: 99;
    padding: 54px 0px 40px 0px;
    p {
      margin: 20px 0;
    }
    .blue-btn {
      width: 80%;
      max-width: 280px;
      height: 48px;
      cursor: pointer;
    }
  }
}
