.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(100%);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.js-focus-visible .focus-visible,
:not(.js-focus-visible) :focus {
  outline-width: 2px;
  outline-style: groove;
  outline-color: white;
}

[type=text],
[type=email],
[type=tel],
[type=password],
[type=search],
[type=number],
textarea {
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0;
  background: none;
  box-shadow: none;
}
[type=text]:invalid,
[type=email]:invalid,
[type=tel]:invalid,
[type=password]:invalid,
[type=search]:invalid,
[type=number]:invalid,
textarea:invalid {
  box-shadow: none;
}

:-moz-ui-invalid {
  box-shadow: none;
}

[type=search]::-webkit-search-decoration,
[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-results-button,
[type=search]::-webkit-search-results-decoration {
  display: none;
}

.page {
  height: 100%;
  margin: 0;
  padding: 0;
}

.page__body {
  height: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

body, html {
  width: 100vw;
  margin: auto;
  overflow: hidden;
  overflow-y: scroll;
  font-family: "Noto Sans TC", Arial, Helvetica, sans-serif;
}

.index header {
  width: 100%;
  max-width: 1800px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 60px 61px 41px 50px;
}
@media (max-width: 868px) {
  .index header {
    justify-content: center;
  }
}
@media (max-width: 1366px) {
  .index header {
    padding: 72px 61px 40px 50px;
  }
}
@media (max-width: 868px) {
  .index header {
    padding: 72px 0px 40px 0px;
  }
}
.index header img {
  height: 60px;
}
@media (max-width: 868px) {
  .index header img {
    height: auto;
    width: 340px;
  }
}
@media (max-width: 320px) {
  .index header img {
    height: auto;
    width: 90%;
  }
}
.index header select {
  border: 1px solid #3D3D3D;
  border-radius: 24px;
  padding: 13px 18px;
  width: 144px;
  height: 48px;
  background-position-x: 110px;
